import * as React from 'react';
import { VPage, Page } from 'tonva';
import { observer } from 'mobx-react';
import { CCustomer } from './CCustomer';

export class VCustomerChek extends VPage<CCustomer> {

    private result: number;
    async open(param: any) {
        this.result = param;
        this.openPage(this.page, param);
    }

    comeBack = () => {
        this.closePage(1);
    }
    private page = observer((param: any) => {

        var show: any;
        if (this.result == 1) {
            show = <span className="px-4"></span>;
        } else {
            show = <span className="px-4"></span>;
        }
        return <Page header='' headerClassName='bg-primary py-1'>
            <div className="w-100  text-muted my-4 px-4">
                {show}
            </div>
            <div className="w-100 text-center">
                <label className="text-success" onClick={this.comeBack}></label>
            </div>
        </Page>
    })
}