import * as React from 'react';
import { observable } from 'mobx';
import { observer } from 'mobx-react';
import { VPage, Page, PageItems, Schema, UiSchema, UiInputItem, UiRadio, Edit, ItemSchema, nav, UiIdItem, Context } from 'tonva';
import { tv } from 'tonva';
import { LMR, StringProp, ComponentProp, Prop, PropGrid, FA } from 'tonva';
import { CCustomer } from './CCustomer';
import { consts } from '../consts';
import { mobileValidation, nameValidation, emailValidation } from 'nv/tools/inputValidations';

const schema: Schema = [
    { name: 'name', type: 'string' },
    { name: 'telephone', type: 'number' },
    { name: 'email', type: 'string' },
    { name: 'wechat', type: 'string' },
    { name: 'teacher', type: 'string' },
    { name: 'potential', type: 'string' },
    { name: 'research', type: 'string' },
    { name: 'addressString', type: 'string' },
];

export class VCustomerDetail extends VPage<CCustomer> {

    @observable private customer: any;
    private uiSchema: UiSchema = {
        items: {
            name: { widget: 'text', label: '', placeholder: '', rules: nameValidation } as UiInputItem,
            telephone: { widget: 'text', label: '', placeholder: '', rules: mobileValidation } as UiInputItem,
            gender: { widget: 'radio', label: '', defaultValue: 1, list: [{ value: 0, title: '' }, { value: 1, title: '' }] } as UiRadio,
            birthDay: { widget: 'datetime', label: '', placeholder: '' } as UiInputItem,
            email: { widget: 'text', label: '', placeholder: '', rules: emailValidation } as UiInputItem,
            wechat: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            teacher: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            addressString: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            potential: { widget: 'radio', label: '', defaultValue: 1, list: [{ value: 0, title: '10' }, { value: 1, title: '10-30' }, { value: 2, title: '30' }] } as UiRadio,
            research: { widget: 'radio', label: '', defaultValue: 1, list: [{ value: 0, title: '' }, { value: 1, title: '' }, { value: 1, title: '' }, { value: 1, title: '' }] } as UiRadio,
        }
    }

    async open(customer: any) {
        this.customer = customer;
        this.openPage(this.page, customer);
    }

    private onItemChanged = async (itemSchema: ItemSchema, newValue: any, preValue: any) => {
        let { name } = itemSchema;
        this.customer[name] = newValue;
        await this.controller.updateMyCustomer(this.customer);

    }

    private page = observer((param: any) => {
        let { cSalesTask, cCustomerUnit, cCustomer } = this.controller.cApp
        let { showCustomerHistory } = cSalesTask;
        let { id: customerid, unit } = param
        let { showCustomerUnitDetail } = cCustomerUnit;
        let { showInnerCustomerSelect: showCustomerSelect } = cCustomer;
        let { innerCustomer, IsOccupy, showCustomerCustomerChek } = this.controller;

        let onshowCustomerHistory = async () => await showCustomerHistory(customerid);
        let onshowCustomerUnitDetail = async () => await showCustomerUnitDetail(unit);
        let onshowCustomerSelect = async () => await showCustomerSelect(customerid);
        let onshowCustomerCustomerChek = async () => await showCustomerCustomerChek(this.customer);

        let rows: Prop[] = [
            {
                type: 'component',
                name: 'customer',
                component: <LMR className="cursor-pointer w-100 py-3" onClick={onshowCustomerUnitDetail}
                    left={< div > <small><FA name='university' className='text-info' /></small> &nbsp;{tv(unit, v => v.name)}</div>}
                    right={< div className="w-2c text-right" > <i className="fa fa-chevron-right small" /></div >}>
                </LMR >,
            } as ComponentProp,
            {
                type: 'component',
                name: 'customer',
                component: <LMR className="cursor-pointer w-100 py-3" onClick={onshowCustomerHistory}
                    left={< div > <small><FA name='hand-o-right' className='text-info' /></small> &nbsp;</div>}
                    right={< div className="w-2c text-right" > <i className="fa fa-chevron-right small " /></div >}>
                </LMR >,
            } as ComponentProp,
            /**{
                type: 'component',
                name: 'customer',
                component: <LMR className="cursor-pointer w-100 py-3" onClick={onshowCustomerSelect}
                    left={< div > <small><FA name='share-alt' className='text-info' /></small> &nbsp;</div>}
                    right={< div className="w-2c text-right" > <i className="fa fa-chevron-right small" /></div >}>
                    <div className="px-3"> {innerCustomer && tv(innerCustomer, v => v.name)}</div>
                </LMR >,
            } as ComponentProp**/
        ];

        var header: any = <span></span>;
        let footer = <button type="button" className="btn btn-danger flex-grow-1 mx-3 my-1 w-100" onClick={onshowCustomerCustomerChek} ></button>;
        return <Page header={header} headerClassName={consts.headerClass} footer={footer}>
            <PropGrid className="my-2" rows={rows} values={this.customer} alignValue="right" />
            <Edit
                schema={schema}
                uiSchema={this.uiSchema}
                data={this.customer}
                onItemChanged={this.onItemChanged} />
        </Page>
    })
}